(function(){
    'use strict';

    // Constant values
    var openweathermapAPPID = "207a941d81bb0d89109c0ce6af589bec"; // The ID used to connect to the openweathermap API (free account, mime@dialgika.de)
    var celciusUnicode =  "\u2103"; // Unicode for â„ƒ

    // The Office initialize function must be run each time a new page is loaded.
    Office.initialize = function(reason){
        $(document).ready(function(){
            retrieveMailData();
            retrieveWeatherData("saarbruecken", "en", "metric");

            $("#info-container").css("display", "inline");
        })
    };

    function retrieveMailData(){
        var sender = JSON.parse(localStorage.getItem("sender"));
        var subject = localStorage.getItem("subject");
        var attachments = JSON.parse(localStorage.getItem("attachments"));

        loadMailData(sender.emailAddress, subject, attachments);
    }

    function retrieveWeatherData(location, language, unit){
        // TODO: cache results of openweathermap to avoid reaching limit
        var urlOpenweathermapAPI = "https://api.openweathermap.org/data/2.5/weather?q="+ location + "&appid=" + openweathermapAPPID + "&units=" + unit + "&lang=" + language;
        $.ajax({
            url: urlOpenweathermapAPI,
            type: 'GET',
            error: function(xhr,status,error){
                //TODO: Proper error handling
                $("#weather-id").text("ERROR: xhr: " + xhr + ", status: " + status + ", error: " + error);
            }
        }).done(function(data){
            loadWeatherData(data.name, data.weather[0].description, data.main.temp);
            console.log("SOI-LOG: Current weather retrieved");
        })
    }

    function loadMailData(senderEmailAddress, subject, attachments = []){
        if(senderEmailAddress == null || subject == null || attachments == null){
            // TODO: Couldnt retrieve data, display error
            return;
        }
        $("#sender-id").text(senderEmailAddress);
        $("#subject-id").text(subject); 
        if(attachments.length > 0){
            console.log("SOI-LOG: attachments found");
            $("#attachments-container").css("display", "inline");
            for(let i = 0; i < attachments.length; i++){
                var attachment = attachments[i];
                $("#attachments-id").append("<li>" +attachment.name + ", "+ attachment.size.formatBytes() +"</li>")
            }
        }
    }

    function loadWeatherData(location, weather, temperature){
        $("#location-id").text(location);
        $("#weather-id").text(weather);
        $("#temperature-id").text(temperature + celciusUnicode);
    }

    
    // formatBytes taken from: https://stackoverflow.com/questions/15900485/correct-way-to-convert-size-in-bytes-to-kb-mb-gb-in-javascript
    Number.prototype.formatBytes = function() {
        var units = ['B', 'KB', 'MB', 'GB', 'TB'],
            bytes = this,
            i;
     
        for (i = 0; bytes >= 1024 && i < 4; i++) {
            bytes /= 1024;
        }
     
        return bytes.toFixed(2) + units[i];
    }

  })();

